.color{ color: #002f34;}
.header{
    top:0px;
    left: 0px;
    right: 0px;
    height: 70px;
    z-index: 9;
    background: #f1f1f1;
    box-shadow: 0px 2px 2px #dadada;
    padding: 0px 18%;
}
.header.logo{
    width: 50px;
    margin-right: 20px;
}
.header.logo img{
    width: 100%;
}

.header .location{ 
    background: #fff;
    border: 2px #002f34 solid;
    border-radius: 5px;
}
.header .location.ico{
    padding: 10px;
}
.header .location .label{
    flex: 1;
    padding: 10px;
    border: 0px;
}
.header .location .arro{
    flex: 1;
    padding: 10px;
    border: 0px;
    background: #fff;
}

.header .search{
    background: #fff;
    border: 2px #002f34 solid;
    border-radius: 5px;
    flex:2;
    margin: 0px 20px;

}
.header .search .query{
    flex: 1;
    padding: 10px;
    border: 0px;
}
.header .search .go{
    flex: 1;
    padding: 10px;
    border: 0px;
    background: #002f34;
    max-width: 50px;
}
.header .search .goHovere{}

.header .actions{
    flex: 1;
    max-width: 200px;
    align-items: flex-end;
}
.header .actions a{
    margin: 0px 20px 10px 0px;
}
.header .actions .sell{
    padding: 8px 20px;
    border: 3px #ffa500 solid;
    text-align: center;
    background: #fff;
    border-radius: 30px;
    box-shadow: 0px 3px 5px #a9a9a9;
}
.header .actions .sell .ico{
    margin-right: 6px;
}
.header .actions .sell:hover{}

.hnav{
    top:70px;
    left: 0px;
    right: 0px;
    height: 50px;
    z-index: 8;
    background: #fff;
    box-shadow: 0px 2px 2px rgb(218, 218,218/ 36%);
    padding: 0px 18%;
}
.hnav .view-cates{
    border: 0px;
    padding: 10px 15px;
    background: rgba(0,0,0,0);;
}
.hnav .view-cates .arro{
    padding: 3px;
    border: 0px;
    background: rgba(0,0,0,0);
    margin-left: 10px;
}
.hnav .view-cates:hover{
    background-color: #f1f1f1;
}
.hnav a{
    padding: 15px;
    margin-right: 2px;
}
.hclr{height: 120px;}

.home-page{
    padding: 30px 18%;
}
.ad-list{
    flex-wrap: wrap;
}

.ad-item{
    min-width: 278px;
    min-height: 266px;
    max-width: 270px;
    max-height: 266px;
    flex-basis: 20%;
    margin: 8px;
    border: 1px #ddd solid;
    padding: 8px;
}
.ad-item .poster{
    height: 160px;
}
.ad-item .poster.placeholder{}
.ad-item .title{
    margin: 10px 0px;
}
.ad-item .title.placeholder{
    height: 25px;
    width: 75%;
}
.ad-item .tagline{}
.ad-item .tagline.placeholder{
    height: 20px;
    width: 90px;
}
.ad-item .ftr{
    margin-top: 15px;
}
.ad-item .ftr .location{
    flex: 2;
}
.ad-item .ftr .location.placeholder{
    height: 12px;
    max-width: 100px;
}
.ad-item .ftr .stamp{
    flex: 1;
}
.ad-item .ftr .stamp.placeholder{
    height: 0px;
}
.load-more{
    margin: 40px auto;
    display: table;
    border: 0px;
    background: #fff;
    padding: 12px 25px;
    border-radius: 10px;
    box-shadow: inset 0px 0px 0px 5px #012f34;
    color: #012f34;

}
.load-more:hover{
    box-shadow: inset -1px -1px 0px 25px #012f34;
    color: #fff;
}

.app-ribbon{
    padding: 0px 18%;
    background: #f9f9f9;
}
